.ContactInfo {
    background-image: url(./background1.jpg);
    border-radius: 0.25rem;
}

.contactTitle {
    color: #ced4da;
}

.error {
    color: red;
}

.contactButton {
    color: #212529;
    background-color: #f8f9fa;
    border-color: #f8f9fa;;
    font-weight: 300;
    border-radius: 0.25rem;
    padding: 0.375rem 0.75rem;
}

.ContactInfo input {
    padding: 0.375rem 0.75rem;
    margin-bottom: 0.8em;
    border-radius: 0.25rem; 
    border: 1px solid #ced4da;
}