.About .avatar {
    width: 101px;
    border-radius: 60px;
    border: 1px solid #005aff;
}

.About .bio {
    padding: 0.375rem 0.75rem;
    background-color: gray;
    border-radius: 0.25rem;
    font-size: larger;
    color: white;
    max-width: 600px;
    text-align: justify;
    margin: auto;
    margin-top: 30px;
    line-height: 1.9;
}

.About {
    border-radius: 0.25rem;
    background-image: url(./background0.jpg);
    background-repeat: no-repeat;
    background-position: center;
}