.NavStyle {
  height: 100px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  max-width: 100vw;
}

.NavStyle h3 a {
  color: #000;
  text-decoration: none;
}

.firstLink a{
  border-bottom-left-radius: 0.25rem;
}

.lastLink  a {
  border-bottom-right-radius: 0.25rem;
}

.header li {
    float: left;
    display: block;
}
  
.header li> a {
  display: block;
  padding: 8px;
  background-color: #dddddd;
  text-decoration: none;
  color: black;
}

.active {
  background-color: #191717;
  color: white;
}

.dev-name {
  margin: 6px;
}